<template>
  <v-container>
    <v-row class="mb-2 mt-2" justify="center">
      <v-col>
        <v-row justify="center">
          <v-btn @click="openDialogSwagger1">Cargar swagger antiguo</v-btn>
        </v-row>
      </v-col>
      <v-col>
        <v-row justify="center">
          <v-btn @click="openDialogSwagger2">Cargar swagger nuevo</v-btn>
        </v-row>
      </v-col>
    </v-row>
    <v-row class="mb-10" justify="center">
      <v-btn @click="compareSwaggers" :disabled="!swagger1 || !swagger2">Comparar</v-btn>
    </v-row>
    <v-divider />
    <v-tabs v-model="tab" grow>
      <v-tab key="0">Árbol</v-tab>
      <v-tab key="1">Tabla</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item key="0">
        <v-row class="mt-10">
          <v-treeview :items="items">
            <template v-slot:label="{ item }">
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <span v-bind="attrs" v-on="on">
                    <span :class="item.color">{{ item.name }}</span>
                    <span v-if="item.parts">"</span>
                    <span
                      v-for="part in item.parts"
                      :key="part.id"
                      :class="
                        part.added
                          ? 'green--text'
                          : part.removed
                          ? 'red--text text-decoration-line-through'
                          : 'black--text'
                      "
                      >{{ part.value }}</span
                    >
                    <span v-if="item.parts">"</span>
                  </span>
                </template>
                <v-row>
                  <v-col v-if="item.parts">
                    <span
                      v-for="part in item.parts"
                      :key="part.id"
                      :class="
                        part.added
                          ? 'green--text'
                          : part.removed
                          ? 'red--text text-decoration-line-through'
                          : 'white--text'
                      "
                      >{{ part.value }}</span
                    >
                  </v-col>
                  <v-col v-if="item.before" class="text--lighten-3 red--text">{{ item.before }}</v-col>
                  <v-col v-if="item.after" class="text--lighten-2 green--text">{{ item.after }}</v-col>
                  <v-col v-if="item.both" class="white--text">{{ item.both }}</v-col>
                </v-row>
              </v-tooltip>
            </template>
          </v-treeview>
        </v-row>
      </v-tab-item>
      <v-tab-item key="1">
        <v-radio-group v-model="filterSelected" row @change="filterTable">
          <v-radio label="Todo" value="todo"></v-radio>
          <v-radio label="Seguridad" value="seguridad"></v-radio>
          <v-radio label="Métodos" value="metodos"></v-radio>
          <v-radio label="Parámetros" value="parametros"></v-radio>
        </v-radio-group>
        <!--<v-row>
          <v-col>
            <v-checkbox v-model="filterSelected" @change="filterTable" label="Seguridad" value="Seguridad"></v-checkbox>
          </v-col>
          <v-col>
            <v-checkbox v-model="filterSelected" @change="filterTable" label="Métodos" value="Métodos"></v-checkbox>
          </v-col>
          <v-col>
            <v-checkbox
              v-model="filterSelected"
              @change="filterTable"
              label="Parámetros"
              value="Parámetros"
            ></v-checkbox>
          </v-col>
          <v-spacer />
        </v-row>-->
        <v-row class="mt-10">
          <v-col>
            <v-data-table :items="filteredItemsTable" :headers="headersTable">
              <!-- vetur uses old eslint version and won't allow to use v-slot modifier -->
              <!-- eslint-disable-next-line -->
              <template v-slot:item.parts="{ item }">
                <span
                  v-for="part in item.parts"
                  :key="part.id"
                  :class="
                    part.added ? 'green--text' : part.removed ? 'red--text text-decoration-line-through' : 'black--text'
                  "
                  >{{ part.value }}</span
                >
              </template>
              <!-- eslint-disable-next-line -->
              <template v-slot:item.action="{ item }">
                <span
                  :class="
                    item.action === 'Añadido'
                      ? 'green--text'
                      : item.action === 'Eliminado'
                      ? 'red--text'
                      : 'black--text'
                  "
                  >{{ item.action }}</span
                >
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-tab-item>
    </v-tabs-items>
    <v-dialog v-model="dialogSwagger1" hide-overlay persistent width="900">
      <v-card>
        <v-card-text>
          <v-textarea
            rows="15"
            :error="swagger1TextError"
            :errorMessages="swagger1TextErrorMessages"
            v-model="swagger1Text"
          ></v-textarea>
        </v-card-text>
        <v-card-actions class="pb-5 justify-center">
          <v-btn @click="parseSwagger1">Aceptar</v-btn>
          <v-btn @click="closeDialogSwagger1">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogSwagger2" hide-overlay persistent width="900">
      <v-card>
        <v-card-text>
          <v-textarea
            rows="15"
            :error="swagger2TextError"
            :errorMessages="swagger2TextErrorMessages"
            v-model="swagger2Text"
          ></v-textarea>
        </v-card-text>
        <v-card-actions class="pb-5 justify-center">
          <v-btn @click="parseSwagger2">Aceptar</v-btn>
          <v-btn @click="closeDialogSwagger2">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import * as differ from 'diff'

const COLOR = {
  MODIFICADO: 'orange--text',
  NUEVO: 'green--text',
  ELIMINADO: 'red--text',
  NORMAL: 'black--text',
  NORMAL_CLARO: 'white--text'
}

const SeguridadRegex = /x-/
const MetodosRegex = /\.paths \.\/(\w|\{|\}|\/)*( \.\w+)?$/
const ParametrosRegex = /\.parameters/

export default {
  name: 'SwaggerCompare',
  data: () => ({
    items: [],
    itemsTable: [],
    filteredItemsTable: [],
    filterSelected: 'todo',
    tab: 0,
    headersTable: [
      { text: 'Ruta', value: 'path', width: '20%' },
      { text: 'Acción', value: 'action', width: '20%' },
      { text: 'Antes', value: 'before', width: '20%' },
      { text: 'Después', value: 'after', width: '20%' },
      { text: 'Diferencia', value: 'parts', width: '20%' }
    ],
    swagger1: null,
    swagger2: null,
    dialogSwagger1: false,
    dialogSwagger2: false,
    swagger1Text: '',
    swagger1TextError: false,
    swagger1TextErrorMessages: '',
    swagger2Text: '',
    swagger2TextError: false,
    swagger2TextErrorMessages: ''
  }),
  methods: {
    openDialogSwagger1() {
      this.dialogSwagger1 = true
      this.swagger1Text = this.swagger1 ? JSON.stringify(this.swagger1) : ''
    },
    openDialogSwagger2() {
      this.dialogSwagger2 = true
      this.swagger2Text = this.swagger2 ? JSON.stringify(this.swagger2) : ''
    },
    closeDialogSwagger1() {
      this.dialogSwagger1 = false
    },
    closeDialogSwagger2() {
      this.dialogSwagger2 = false
    },
    parseSwagger1() {
      try {
        this.swagger1 = JSON.parse(this.swagger1Text)
        this.dialogSwagger1 = false
        this.items = []
        this.itemsTable = []
        this.filteredItemsTable = []
      } catch (e) {
        this.swagger1TextError = true
        this.swagger1TextErrorMessages = 'Formato de JSON no válido'
      }
    },
    parseSwagger2() {
      try {
        this.swagger2 = JSON.parse(this.swagger2Text)
        this.dialogSwagger2 = false
        this.items = []
        this.itemsTable = []
        this.filteredItemsTable = []
      } catch (e) {
        this.swagger2TextError = true
        this.swagger2TextErrorMessages = 'Formato de JSON no válido'
      }
    },
    compareSwaggers() {
      this.items = []
      this.itemsTable = []
      this.filteredItemsTable = []
      this.compareSwaggersObject(this.swagger1, this.swagger2, this.items, '', true)
      this.filterSelected = 'todo'
      this.filteredItemsTable = this.itemsTable
    },
    filterTable() {
      this.filteredItemsTable = this.itemsTable.filter((element) => {
        let passFilters = false
        if (this.filterSelected === 'todo') {
          passFilters = true
        }
        if (this.filterSelected === 'seguridad') {
          if (SeguridadRegex.test(element.path)) passFilters = true
        }
        if (this.filterSelected === 'metodos') {
          if (MetodosRegex.test(element.path)) passFilters = true
        }
        if (this.filterSelected === 'parametros') {
          if (ParametrosRegex.test(element.path)) passFilters = true
        }
        return passFilters
      })
    },
    compareSwaggersObject(node1, node2, dest, path, addToTable) {
      let equals = true
      if (Array.isArray(node1) && Array.isArray(node2)) {
        equals = this.compareSwaggersArray(node1, node2, dest, path, addToTable)
      } else {
        let node1Attrs = Object.keys(node1)
        let node2Attrs = Object.keys(node2)

        for (let attr of node1Attrs) {
          if (!node2Attrs.includes(attr)) {
            equals = false
            let nodeIsObject = typeof node1[attr] === 'object'
            this.addDeletedElementToTree(attr, dest, nodeIsObject, node1[attr], path)
            this.addDeletedElementToTable(attr, path, addToTable, JSON.stringify(node1[attr]), '')
          } else {
            let nodeIsObject = typeof node1[attr] === 'object'
            let childrenEquals = this.addModifiedElementToTree(attr, dest, nodeIsObject, node1[attr], node2[attr], path)
            equals &= childrenEquals
            let addToTableChild = addToTable && !nodeIsObject && !childrenEquals
            this.addModifiedElementToTable(attr, path, addToTableChild, node1[attr], node2[attr])
          }
        }

        for (let attr of node2Attrs) {
          if (!node1Attrs.includes(attr)) {
            equals = false
            let nodeIsObject = typeof node2[attr] === 'object'
            this.addNewElementToTree(attr, dest, nodeIsObject, node2[attr], path)
            this.addNewElementToTable(attr, path, addToTable, '', JSON.stringify(node2[attr]))
          }
        }
      }
      return equals
    },
    compareSwaggersArray(node1, node2, dest, path, addToTable) {
      let equals = true

      for (let attr of node1) {
        let attr2 = node2.find((e) => e.name === attr.name)
        if (!attr2) {
          equals = false
          let nodeIsObject = typeof attr === 'object'
          this.addDeletedElementToTree('[i]', dest, nodeIsObject, attr, path)
          this.addDeletedElementToTable('[i]', path, addToTable, JSON.stringify(attr), '')
        } else {
          let nodeIsObject = typeof attr === 'object'
          let childrenEquals = this.addModifiedElementToTree('[i]', dest, nodeIsObject, attr, attr2, path)
          equals &= childrenEquals
          let addToTableChild = addToTable && !nodeIsObject && !childrenEquals
          this.addModifiedElementToTable('[i]', path, addToTableChild, attr, attr2)
        }
      }

      for (let attr2 of node2) {
        let attr = node1.find((e) => e.name === attr2.name)
        if (!attr) {
          equals = false
          let nodeIsObject = typeof attr2 === 'object'
          this.addNewElementToTree('[i]', dest, nodeIsObject, attr2, path)
          this.addNewElementToTable('[i]', path, addToTable, '', JSON.stringify(attr2))
        }
      }
      return equals
    },
    addDeletedElementToTree(attr, dest, isObject, oldValue, path) {
      let itemChild = {
        id: uuidv4(),
        name: attr,
        color: COLOR.ELIMINADO
      }
      if (isObject) {
        itemChild.children = []
        this.compareSwaggersObject(oldValue, {}, itemChild.children, `${path} .${attr}`, false)
      } else {
        itemChild.name += `: "${oldValue}"`
      }
      dest.push(itemChild)
    },
    addNewElementToTree(attr, dest, isObject, newValue, path) {
      let itemChild = {
        id: uuidv4(),
        name: attr,
        color: COLOR.NUEVO
      }
      if (isObject) {
        itemChild.children = []
        this.compareSwaggersObject({}, newValue, itemChild.children, `${path} .${attr}`, false)
      } else {
        itemChild.name += `: "${newValue}"`
      }
      dest.push(itemChild)
    },
    addModifiedElementToTree(attr, dest, isObject, oldValue, newValue, path) {
      let itemChild = {
        id: uuidv4(),
        name: attr
      }
      dest.push(itemChild)
      let childrenEquals
      if (isObject) {
        itemChild.children = []
        childrenEquals = this.compareSwaggersObject(oldValue, newValue, itemChild.children, `${path} .${attr}`, true)
        let color = childrenEquals ? COLOR.NORMAL : COLOR.MODIFICADO
        itemChild.color = color
      } else {
        itemChild.name += ': '
        childrenEquals = oldValue === newValue
        let color = childrenEquals ? COLOR.NORMAL : COLOR.MODIFICADO
        itemChild.color = color
        itemChild.parts = differ.diffWords(oldValue.toString(), newValue.toString())
      }
      return childrenEquals
    },
    addDeletedElementToTable(attr, path, addToTable, oldValue, newValue) {
      if (addToTable) {
        this.itemsTable.push({
          path: `${path} .${attr}`,
          action: 'Eliminado',
          before: oldValue.toString(),
          after: newValue.toString(),
          parts: differ.diffWords(oldValue.toString(), newValue.toString())
        })
      }
    },
    addNewElementToTable(attr, path, addToTable, oldValue, newValue) {
      if (addToTable) {
        this.itemsTable.push({
          path: `${path} .${attr}`,
          action: 'Añadido',
          before: oldValue.toString(),
          after: newValue.toString(),
          parts: differ.diffWords(oldValue.toString(), newValue.toString())
        })
      }
    },
    addModifiedElementToTable(attr, path, addToTable, oldValue, newValue) {
      if (addToTable) {
        this.itemsTable.push({
          path: `${path} .${attr}`,
          action: 'Modificado',
          before: oldValue.toString(),
          after: newValue.toString(),
          parts: differ.diffWords(oldValue.toString(), newValue.toString())
        })
      }
    }
  }
}
</script>